/*--------------------------------------------------------------------------------------------------------------------------*/
/*-----------------------------------------Header---------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------------------------------------*/

nav {
  position:fixed !important;
  top:0px;
  width:100%;
  z-index:5;
}

.navbar-brand img{
  max-width:137px;
}

.nav-item {
  padding-left:10px;
  padding-right:10px;
}

ul {
  background-color:#222222;
  padding:10px;
}

li a {
  color:white !important;
  line-height:30px;
}

a:link {
  text-decoration: none !important;
}

li a:hover {
  color:lightblue !important;
}

.dropdown .nav-link {
  padding:0px;
  padding-left:20px;
}

.no-padding-nav {
  padding:0px !important;
  padding-left:20px !important;
}

.padded-nav {
  padding-left:20px;
}

/*--------------------------------------drop-down menu media query----------------------------------------------------------*/
@media only screen and (max-width: 767px) {
  .dropdown-menu {
    display: block !important;
    background-color:#f8f9fa !important;
    border:none !important;
    padding:0px !important;
  }

  button.dropdown-item {
    padding:0px !important;
    margin-left:30px !important;
  }

  button.dropdown-item:hover {
    color:lightblue !important;
  }

  .navbar-nav {
    background-color:#f8f9fa !important;
  }

  .navbar-nav a {    
    color:#222 !important;
  }

  .no-padding-nav:hover {
    color:#222 !important;
  }
}

